import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
import Select from 'react-select'
import { NavLink } from 'react-router-dom'
import { withRouter } from 'react-router'

const Navigation = withRouter(inject('store')(observer(class extends Component {

  componentDidMount = () => {
    const { store, location } = this.props
    const query = new URLSearchParams(location.search)
    const searchQuery = query.get('query')
    const pageQuery = query.get('page')
    const perPageQuery = query.get('per_page')
    store.getSongBooks(searchQuery, pageQuery, perPageQuery)
    store.getUser()
  }

  render() {
    const { store } = this.props
    return (
			<div id="navbar" className="navbar-collapse collapse">
				<ul className="nav navbar-nav">
					<li><NavLink exact to="/" activeClassName="active" className="navlink">Psn</NavLink></li>
					<li><NavLink to="/songbook" activeClassName="active" className="navlink">Zpvnky</NavLink></li>
					{/* <li><NavLink to="/" activeClassName="active" className="navlink">Autoi</NavLink></li> */}
				</ul>
				<ul className="nav navbar-nav navbar-right">
					<li style={{ marginTop: '15px' }}>Aktivn zpvnk:</li>
					<li style={{ minWidth: '200px', padding: '8px 15px' }}>
						<Select                  
							options={store.songBooksToSelect}
							value={store.activeSongBookToSelect}
							onChange={payload => store.setActiveSongBook(payload.value)}
						/>
					</li>
					<li><a>{store.user.name}</a></li>
					<li><a href={store.user.logoutLink}>odhlsit</a></li>
				</ul>
			</div>
    )
  }
})))

export default Navigation



